<template>
  <div class="equipment-affiliation-details">
    <modal
      title="设备归属"
      :visible="visible"
      :visibleMask="!visibleEquipment"
      width="44.583vw"
      height="30.781vw"
      @close="$emit('close')"
    >
      <img
        slot="modalBg"
        class="modal-bg"
        src="../../assets/img/components/img-edit2.png"
        alt=""
      />
      <div class="affiliation-content">
        <a-form-model :model="form" :layout="formLayout">
          <a-form-model-item label="归属名称：">
            <a-input class="default-input" v-model="form.name"></a-input>
          </a-form-model-item>
          <a-form-model-item label="归属名称：">
            <a-select
              class="default-select"
              dropdown-class-name="default-select-dropdown"
              v-model="form.type"
              :get-popup-container="triggerNode => triggerNode.parentNode"
            >
              <a-select-option value="1">
                类别1
              </a-select-option>
              <a-select-option value="2">
                类别2
              </a-select-option>
            </a-select>
          </a-form-model-item>
          <a-form-model-item label="归属名称：">
            <vuedraggable
              class="left"
              v-model="equipments"
              group="people"
              draggable=".item"
              :options="dragOptions"
            >
              <transition-group class="items" tag="div">
                <div
                  v-for="(item, index) in equipments"
                  :key="index"
                  class="item"
                >
                  <div class="numer">{{ index + 1 }}</div>
                  <p>{{ item }}</p>
                </div>
                <div class="add" key="add" @click="openDetailsList">
                  <span class="iconfont icon-jiahao"></span>
                </div>
                
              </transition-group>
            </vuedraggable>
          </a-form-model-item>
        </a-form-model>
      </div>
      <div class="affiliation-bottom"></div>
    </modal>
    <detailsList
      :visible="visibleEquipment"
      @close="closeDetailsList"
      @saveEquipment="saveEquipment"
    ></detailsList>
  </div>
</template>

<script>
import Modal from "../../components/Modal";
import vuedraggable from "vuedraggable";

import detailsList from "./detailsList";

import moment from "moment";

export default {
  components: {
    Modal,
    vuedraggable,
    detailsList
  },
  props: {
    // 对话框是否可见
    visible: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      formLayout: "inline",
      visibleEquipment: false,
      form: {
        name: "",
        type: "1"
      },
      dragOptions: {
        animation: 120,
        scroll: true,
        group: "sortlist",
        ghostClass: "ghost-style"
      },
      equipments: [
        "车底扫描仪",
        "车底车辆抓拍机",
        "立式人证机",
        "道闸抓拍机",
        "道闸抓拍机",
        "阻车器",
        "道闸抓拍机1",
        "道闸抓拍机1",
        "阻车器1",
        "道闸抓拍机2",
        "道闸抓拍机2",
        "阻车器2",
        "道闸抓拍机3",
        "道闸抓拍机3",
        "阻车器3"
      ]
    };
  },
  methods: {
    moment,

    openDetailsList() {
      this.visibleEquipment = true;
    },

    closeDetailsList() {
      this.visibleEquipment = false;
    },

    saveEquipment(equipments) {
      let self = this;
      equipments.map(equipment => {
        self.equipments.push(equipment.name);
      });
      this.closeDetailsList();
    }
  }
};
</script>

<style lang="less" scoped></style>
