<template>
  <div class="equipment-affiliation-details-list">
    <modal
      title="设备列表"
      :visible="visible"
      width="39.948vw"
      height="29.688vw"
      @close="$emit('close')"
    >
      <img
        slot="modalBg"
        class="modal-bg"
        src="../../assets/img/components/img-equipment-affiliation-modal-bg.png"
        alt=""
      />
      <div class="details-list-search">
        <a-form-model :model="form" :layout="formLayout">
          <a-form-model-item label="归属名称：">
            <a-input class="default-input" v-model="form.name"></a-input>
          </a-form-model-item>
          <a-form-model-item label="归属名称：">
            <a-select
              class="default-select"
              dropdown-class-name="default-select-dropdown"
              v-model="form.type"
              :get-popup-container="triggerNode => triggerNode.parentNode"
            >
              <a-select-option value="1">
                类别1
              </a-select-option>
              <a-select-option value="2">
                类别2
              </a-select-option>
            </a-select>
          </a-form-model-item>
        </a-form-model>
      </div>
      <div class="details-list-contents">
        <a-table
          :row-selection="{
            selectedRowKeys: selectedRowKeys,
            onChange: onSelectChange
          }"
          class="default-table"
          :columns="columns"
          :data-source="data"
          :pagination="false"
          :scroll="{ y: '14.6vw' }"
        >
        </a-table>

        <a-pagination
          v-model="current"
          :total="total || 1"
          :page-size="defaultPageSize"
          :item-render="itemRender"
          @change="onPaginationChange"
        />
      </div>
      <!-- 表格展示 end -->

      <!-- 分页 start -->

      <div class="details-list-bottom">
        <a-button class="form-button" type="default" @click="$emit('close')">取消</a-button>
        <a-button class="form-button" type="primary" @click="saveEquipment"
          >保存</a-button
        >
      </div>
    </modal>
  </div>
</template>

<script>
import Modal from "../../components/Modal";
import vuedraggable from "vuedraggable";

import moment from "moment";

const columns = [
  {
    title: "序列",
    dataIndex: "sequence",
    key: "sequence",
    width: "3vw"
  },
  {
    title: "标识",
    dataIndex: "type",
    key: "type",
    width: "5vw"
  },
  {
    title: "名称",
    dataIndex: "name",
    key: "name",
    ellipsis: true,
    width: "5vw"
  },
  {
    title: "品牌型号",
    dataIndex: "brandModels",
    key: "brandModels",
    ellipsis: true,
    width: "5vw"
  },
  {
    title: "录入时间",
    dataIndex: "time",
    key: "time",
    ellipsis: true,
    width: "10vw"
  }
]; //表头信息

export default {
  components: {
    Modal,
    vuedraggable
  },
  props: {
    // 对话框是否可见
    visible: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      columns: columns,
      data: [],
      selectedRowKeys: [],
      selectedRows: [],
      defaultPageSize: 7,
      total: 100,
      current: 1,
      formLayout: "inline",
      form: {
        name: "",
        type: "1"
      }
    };
  },
  computed: {
    hasSelected() {
      return this.selectedRowKeys.length > 0;
    }
  },
  mounted() {
    for (let i = 0; i < 7; i++) {
      this.data.push({
        key: i,
        sequence: i,
        name: "大车道",
        type: "15uegru",
        brandModels: "大华",
        time: "2021-05-12 10:46:20"
      });
    }
  },
  methods: {
    moment,
    itemRender(current, type, originalElement) {
      if (type === "prev") {
        return "上一页";
      } else if (type === "next") {
        return "下一页";
      } else if (type === "page") {
        return current;
      }
      return originalElement;
    },

    onPaginationChange(page, pageSize) {
      console.log(page, pageSize);
    },
    onSelectChange(selectedRowKeys, selectedRows) {
      this.selectedRows = selectedRows;
      this.selectedRowKeys = selectedRowKeys;
    },
    saveEquipment() {
      this.$emit("saveEquipment", this.selectedRows);
    }
  }
};
</script>

<style lang="less" scoped></style>
