 * @Description: 设备归属
 * @Autor: YML
 * @Date: 2021-05-08 09:33:48
 * @LastEditors: YML
 * @LastEditTime: 2021-05-25 15:36:25
-->
<template>
  <div class="equipment-affiliation">
    <box>
      <!-- 信息筛选 start -->
      <div class="search">
        <div class="search-body">
          <a-form layout="inline" class="search-main">
            <a-form-item label="归属类别：">
              <a-select
              class="default-select"
                dropdown-class-name="default-select-dropdown"
                placeholder="请选择归属类别"
              >
                <a-select-option value="jack">
                  类别1
                </a-select-option>
                <a-select-option value="lucy">
                  类别2
                </a-select-option>
              </a-select>
            </a-form-item>
            <a-form-model-item label="开始时间：" prop="startTime">
              <a-date-picker
                class="default-date-picker"
                show-time
                dropdown-class-name="default-date-picker-dropdown"
                :get-calendar-container="triggerNode => triggerNode.parentNode"
              />
            </a-form-model-item>
            <a-form-model-item label="结束时间：" prop="endTime">
              <a-date-picker
                class="default-date-picker"
                show-time
                dropdown-class-name="default-date-picker-dropdown"
                :get-calendar-container="triggerNode => triggerNode.parentNode"
              />
            </a-form-model-item>
          </a-form>
        </div>
      </div>
      <!-- 信息筛选 end -->

      <!-- 新增按钮 start -->
      <div class="operation-nav">
        <div class="btn-box">
          <a-button class="operation-button">
            <span class="iconfont iconfont-14 icon-sousuo1"></span>
            <span class="btn-title">查询</span>
          </a-button>
          <a-button class="operation-button" @click="setModal()">
            <span class="iconfont iconfont-16 icon-bianji2"></span>
            <span class="btn-title">新增</span>
          </a-button>
        </div>
      </div>
      <!-- 新增按钮 end -->

      <!-- 表格展示 start -->
      <div class="contents">
        <a-table
          class="default-table"
          :columns="columns"
          :data-source="data"
          :pagination="false"
          :scroll="{ y: 'calc(90vh - 15.7vw)' }"
        >
          <div slot="order">
            <div class="order-box">
              <div class="orderterm" v-for="(item, i) in orderList" :key="i">
                {{ item.name }}
              </div>
            </div>
          </div>
          <div slot="operation" class="operation">
            <div class="count" @click="setModal()">
              <span class="iconfont iconfont-18 icon-bianji"></span>
              <span class="title">编辑</span>
            </div>
          </div>
        </a-table>
      </div>
      <!-- 表格展示 end -->

      <!-- 分页 start -->
      <div class="content-pagination">
        <pagination />
      </div>
      <!-- 分页 end -->
    </box>
    <mdetails :visible="visibleModal" @close="setModal"></mdetails>
  </div>
</template>

<script>
import Box from "../../components/Box"; //边框样式组件
import Pagination from "../../components/Pagination"; //分页组件
import mdetails from "./details"; //弹窗组件
export default {
  components: {
    Box,
    Pagination,
    mdetails
  },
  data() {
    return {
      orderList: [
        {
          id: 1,
          name: "车底扫描仪"
        },
        {
          id: 2,
          name: "车底车辆抓拍机"
        },
        {
          id: 3,
          name: "立式人证机"
        },
        {
          id: 4,
          name: "道闸抓拍机"
        },
        {
          id: 5,
          name: "道闸抬杠"
        },
        {
          id: 6,
          name: "阻车器"
        }
      ], //设备列表
      columns: [
        {
          title: "序列",
          dataIndex: "sequence",
          key: "sequence",
          width: "3vw"
        },
        {
          title: "归属名称",
          dataIndex: "name",
          key: "name",
          width: "5vw"
        },
        {
          title: "归属类别",
          dataIndex: "type",
          key: "type",
          width: "5vw"
        },
        {
          title: "设备及顺序",
          dataIndex: "order",
          key: "order",
          width: "36vw",
          scopedSlots: { customRender: "order" }
        },
        {
          title: "时间",
          dataIndex: "time",
          key: "time",
          width: "10vw"
        },
        {
          title: "操作",
          dataIndex: "operation",
          key: "operation",
          width: "5vw",
          scopedSlots: { customRender: "operation" }
        }
      ], //表头信息
      data: [], //表格内容
      selectedRowKeys: [], // 列表项是否可选择是否显示
      visibleModal: false
    };
  },
  mounted() {
    /**
     * 向列表项追加内容
     */
    for (let i = 0; i < 20; i++) {
      this.data.push({
        key: i,
        sequence: i,
        name: "大车道",
        type: "车道",
        time: "2021-05-12 10:46:20"
      });
    }
  },
  methods: {
    /**
     * 编辑列表项
     */
    setModal() {
      console.log("===", this.visibleModal);
      this.visibleModal = !this.visibleModal;
    }
  }
};
</script>

<style lang="less">
@import "./index.less";
</style>
